// Î∂ÑÎãπ 200Îã®Ïñ¥ ÏùΩÎäîÎã§Í≥† ÏπòÍ≥† Í≥ÑÏÇ∞
export function estimateReadTime(content: string) {
  if (!content) return `1 min read`;
  const words = content.trim().split(/\s+/).length;
  const minutes = Math.max(1, Math.ceil(words / 200));
  return `${minutes} min read`;
}

import Parser from "rss-parser"; // üí° [Ï∂îÍ∞Ä]

// 1. RSS Ï£ºÏÜå
const RSS_FEED_URL = "https://blog.google/products/gemini/rss/";

// 2. FeedItem ÌÉÄÏûÖÏùÑ Ïó¨Í∏∞Î°ú Ïù¥Îèô Î∞è export
export type FeedItem = {
  title: string;
  link: string;
  pubDate: string;
  guid: string;
  content: string;
  contentSnippet?: string;
  categories?: string[];
};

// 3. getGeminiProductFeed Ìï®ÏàòÎ•º Ïó¨Í∏∞Î°ú Ïù¥Îèô Î∞è export
export async function getGeminiProductFeed(): Promise<FeedItem[]> {
  try {
    const response = await fetch(RSS_FEED_URL, {
      next: { revalidate: 3600 },
    });

    if (!response.ok) {
      throw new Error(`Failed to fetch RSS feed: ${response.statusText}`);
    }

    const xmlText = await response.text();
    const parser = new Parser<Record<string, never>, FeedItem>();
    const feed = await parser.parseString(xmlText);

    // "Only plain objects" ÏóêÎü¨ Ìï¥Í≤∞ÏùÑ ÏúÑÌïú ÏàúÏàò Í∞ùÏ≤¥ Î≥ÄÌôò
    const plainItems: FeedItem[] = feed.items.map((item) => ({
      title: item.title,
      link: item.link,
      pubDate: item.pubDate,
      guid: item.guid,
      content: item.content,
      contentSnippet: item.contentSnippet,
      categories: item.categories,
    }));

    return plainItems;
  } catch (error) {
    console.error("Error fetching or parsing Gemini RSS feed:", error);
    return [];
  }
}